
import React from "react";
import Select from 'react-select';
import makeAnimated from 'react-select/animated';

import Tabs from "./IndexSections/Tabs";
import {
  Container,

} from "reactstrap";


const animatedComponents = makeAnimated();

const colourStyles = {
  multiValue: (styles,) => {
    return {
      ...styles,
      backgroundColor: "white"
    };
  },
  multiValueLabel: (styles) => ({
    ...styles,
    color: "#172b4d"
  }),
  multiValueRemove: (styles) => ({
    ...styles,
    color: "#172b4d",
    ":hover": {
      backgroundColor: "#172b4d",
      color: "#fff"
    }
  })
};

const carreras=[
  { value: '1', label: 'Informatica',ramos:[
    {value:'1.1', label:'ramo1',otro:'otro1'},
    {value:'1.2', label:'ramo2',otro:'otro2'},
    {value:'1.3', label:'ramo3',otro:'otro3'},

] },
{ value: '2', label: 'Medicina',ramos:[
  {value:'2.1', label:'ramo1',otro:'otro1'},
  {value:'2.2', label:'ramo2',otro:'otro2'},
  {value:'2.3', label:'ramo3',otro:'otro3'},

] },
{ value: '3', label: 'Contruccion',ramos:[
  {value:'3.1', label:'ramo1',otro:'otro1'},
  {value:'3.2', label:'ramo2',otro:'otro2'},
  {value:'3.3', label:'ramo3',otro:'otro3'},

] },
]


class Inicio extends React.Component {

  constructor(props) {
    super(props)
    this.state = {
      boolean : true,
      ramos : [],

      
    }
   this.handleClick = this.handleClick.bind(this);
   this.selectvalue = this.selectvalue.bind(this);
  
  }
  handleClick() {
    this.setState(state => ({
      boolean: !state.boolean
    }));
  }
  
  selectvalue(e) {
    var salida =[]
    if(e !=null){
      for (let i = 0; i < e.length; i++) {
        salida =salida.concat(e[i].ramos);
      }
    }
  
    this.ramos = salida
    this.setState((state, props) => {
      return {ramos: salida};
    });
    
    
  }

  render() {
  
    return (
      <>
        <section>
          <Container>
            <div className="row">
              <div className="col-md-12">

              </div>
              <div className="col-md-6">
                <Select
                closeMenuOnSelect={true}
                components={animatedComponents}
                placeholder="Todas las carreras"
                isMulti
                options={carreras}
                styles={colourStyles}
                onChange={this.selectvalue}

                />
              </div>
              <div className="col-md-6">
                <Select
                closeMenuOnSelect={true}
                components={animatedComponents}
                placeholder="Todos los ramos"
                isMulti
                options={this.ramos}
                styles={colourStyles}
          
                />
              </div>
            </div>
            <Tabs></Tabs>
          </Container>
        </section>
      </>
    );
  }
}

export default Inicio;
